import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import './App.css';
import Menu from './components/Menu'
import Home from './components/Accueil';
import ProfessionalDirectory from './components/Annuaire';
import Outings from './components/Sorties';
import PrivateSales from './components/Ventes_privees';
import DiscountCoupons from './components/Coupons_reduction';
import AllOut from './components/Sortons_ensemble';
import JobOffers from './components/Offres_d_emplois';
import ErrorPage from './components/ErrorPage';
import Header from './components/Header';
import Footer from './components/Footer';
import ProfessionalDirectoryAlpha from './components/AnnuaireAlpha';
import ArticleComplet from './components/ArticleComplet'
import AuPaletDor from './societes/AuPaletDOr';




function App() {


  return (

    <BrowserRouter>

      <Header />

      <Menu />

      <Switch>

        <Route exact path="/" component={Home} />
        <Route exact path="/Accueil" component={Home} />
        <Route exact path="/Annuaire" component={ProfessionalDirectory} />
        <Route exact path="/AnnuaireAlpha" component={ProfessionalDirectoryAlpha} />
        <Route exact path="/Sorties" component={Outings} />
        <Route exact path="/Ventes_privees" component={PrivateSales} />
        <Route exact path="/Coupons_reduction" component={DiscountCoupons} />
        <Route exact path="/Sortons_ensemble" component={AllOut} />
        <Route exact path="/Offres_d_emplois" component={JobOffers} />
        <Route path="/ArticleComplet/:id" component={ArticleComplet} />
        <Route path="/societes/AuPaletDor" component={AuPaletDor} />
        <Route component={ErrorPage} />

      </Switch>

      <Footer />



    </BrowserRouter>
  );

}

export default App;
